<template>
  <div class="spec-filter" v-if="hasSpecs">

    <div class="filter-group" v-if="widthRaw.length > 0">
      <div class="filter-title" @click="toggleSection('width')">
        <span class="toggle-arrow">{{ openSections.width ? '‚ñº' : '‚ñ∂' }}</span>
        <span class="title-text">Í∞ÄÎ°ú</span>
        <label class="precision-toggle" @click.stop>
          <input type="checkbox" v-model="isPrecisionMode"> Ï†ïÎ∞Ä
        </label>
      </div>

      <div class="filter-options" v-show="openSections.width && !isPrecisionMode && Object.keys(widthRanges).length > 0">
        <label v-for="(vals, label) in widthRanges" :key="label" class="filter-label">
          <input type="checkbox" :checked="isRangeSelected('Í∞ÄÎ°ú', vals)" @change="updateMultiValueFilter('Í∞ÄÎ°ú', vals, $event.target.checked)" />
          {{ label }}cm
        </label>
      </div>

      <div class="filter-options" v-show="openSections.width && isPrecisionMode">
        <label v-for="opt in widthRaw" :key="opt" class="filter-label">
          <input type="checkbox" :value="opt" :checked="selectedSpecs['Í∞ÄÎ°ú']?.has(opt)" @change="updateFilter('Í∞ÄÎ°ú', opt, $event.target.checked)" />
          {{ opt }}
        </label>
      </div>
    </div>

    <div class="filter-group" v-for="(options, key) in normalSpecs" :key="key">
      <div class="filter-title" @click="toggleSection(key)">
        <span class="toggle-arrow">{{ openSections[key] ? '‚ñº' : '‚ñ∂' }}</span>
        <span class="title-text">{{ key }}</span>
      </div>
      <div class="filter-options" v-show="openSections[key]">
        <label v-for="opt in options" :key="opt" class="filter-label">
          <input type="checkbox" :value="opt" :checked="selectedSpecs[key]?.has(opt)" @change="updateFilter(key, opt, $event.target.checked)" />
          {{ opt }}
        </label>
      </div>
    </div>

    <div class="filter-group" v-if="Object.keys(otherSpecs).length > 0">
      <div class="filter-title" @click="toggleSection('other')">
        <span class="toggle-arrow">{{ openSections.other ? '‚ñº' : '‚ñ∂' }}</span>
        <span class="title-text">Í∏∞ÌÉÄ</span>
      </div>
      <div class="filter-options" v-show="openSections.other">
        <label v-for="(values, key) in otherSpecs" :key="key" class="filter-label">
          <input type="checkbox" :checked="isRangeSelected(key, values)" @change="updateMultiValueFilter(key, values, $event.target.checked)" />
          {{ key }}
        </label>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, computed, defineProps, defineEmits, watch } from 'vue'

const props = defineProps({
  products: { type: Array, default: () => [] },
  targetProduct: { type: Object, default: null }
})

const emit = defineEmits(['filter-change'])

const selectedSpecs = ref({})
const isPrecisionMode = ref(false)
const openSections = ref({ width: true })

const toggleSection = (key) => {
  openSections.value[key] = !openSections.value[key]
}

watch(isPrecisionMode, () => {
  if (selectedSpecs.value['Í∞ÄÎ°ú']) {
    delete selectedSpecs.value['Í∞ÄÎ°ú']
    emitChange()
  }
})

watch(() => props.targetProduct?.productCode, () => {
  selectedSpecs.value = {}
  emitChange()
})

const excludedKeys = ['Ï†úÏ°∞ÌöåÏÇ¨', 'Ï†úÏ°∞ÏÇ¨', 'ÏïàÏ†ÑÌôïÏù∏Ïù∏Ï¶ù', 'ÏïàÏ†Ñ ÌôïÏù∏Ïù∏Ï¶ù', 'ÏïàÏ†ÑÌôïÏù∏ Ïù∏Ï¶ù', 'Ï†ÅÌï©ÏÑ±ÌèâÍ∞ÄÏù∏Ï¶ù', 'Ï†ÅÌï©ÏÑ± ÌèâÍ∞ÄÏù∏Ï¶ù', 'Ï†ÅÌï©ÏÑ± ÌèâÍ∞Ä Ïù∏Ï¶ù', 'Îì±Î°ùÎÖÑÏõî', 'Ï∂úÏãúÎÖÑÏõî', 'HDMI', 'Í≤åÏûÑÏö©']
const featureKeys = ['Ïä§ÏúôÍ∏∞Îä•', 'Ïä§ÌéÄÏßÄ(Ìèº)', 'Ìó§ÎìúÍ∏∞Îä•', 'ÏÜú', 'Î™®ÏÖòÏä¨ÎùºÏù¥Îî©', 'Ï†úÎ°úÏõî', 'ÏÉùÌôúÎ∞©Ïàò', 'Î∞úÏàò', 'Ïù¥ÏßÄÌÅ¥Î¶∞', 'Î¨¥ÏßÄ', 'ÌîåÎ¶¨Ïä§', 'Î¶¨Î≤ÑÏÑúÎ∏î', 'Ìè¥Î¶¨ÏóêÏä§ÌÑ∞', 'Í∏∞Î™®', 'Î∞©Ìíç', '1Gbps Ïú†ÏÑ†', 'dpÌè¨Ìä∏', 'DPÌè¨Ìä∏', 'ledÏø®Îü¨', 'LEDÏø®Îü¨', 'Ïñ¥Ìï≠Ìòï']

const parseWidthToCm = (val) => {
  if (!val) return 0
  const str = String(val).toLowerCase().replace(/,/g, '').replace(/\s+/g, '')
  const num = parseFloat(str)
  if (isNaN(num)) return 0
  if (str.includes('mm')) return num / 10
  if (str.includes('cm')) return num
  if (str.includes('m') && !str.includes('mm')) return num * 100
  if (num >= 500) return num / 10
  return num
}

const specs = computed(() => {
  const products = props.products
  const target = props.targetProduct
  const tempNormal = {}; const tempOther = {}; const tempWidthRaw = new Set(); const tempWidthRanges = {}
  let targetKeys = []
  if (target?.specifications) {
    try {
      const obj = typeof target.specifications === 'string' ? JSON.parse(target.specifications) : target.specifications
      targetKeys = Object.keys(obj).map(k => k.trim())
    } catch (e) {
      // ESLint Î∞©ÏßÄÎ•º ÏúÑÌï¥ Î°úÍ∑∏ Ï∂îÍ∞Ä
      console.warn("Target spec parse fail", e)
    }
  }

  products.forEach(p => {
    if (!p.specifications) return
    try {
      const specObj = typeof p.specifications === 'string' ? JSON.parse(p.specifications) : p.specifications
      Object.keys(specObj).forEach(rawKey => {
        const key = rawKey.trim()
        if (targetKeys.length > 0 && !targetKeys.includes(key)) return
        if (excludedKeys.includes(key)) return
        const val = specObj[key]
        if (key === 'Í∞ÄÎ°ú') {
          tempWidthRaw.add(val)
          const cm = parseWidthToCm(val)
          if (cm > 0) {
            const lower = Math.floor(cm / 20) * 20
            const upper = lower + 20
            const label = `${lower}~${upper}`
            if (!tempWidthRanges[label]) tempWidthRanges[label] = new Set()
            tempWidthRanges[label].add(val)
          }
          return
        }
        if (featureKeys.includes(key)) {
          if (!tempOther[key]) tempOther[key] = new Set()
          tempOther[key].add(val)
        } else {
          if (!tempNormal[key]) tempNormal[key] = new Set()
          tempNormal[key].add(val)
        }
      })
    } catch (e) {
      // 142Ìñâ Í∑ºÏ≤ò ÏóêÎü¨ Ìï¥Í≤∞: Îπà Î∏îÎ°ùÏóê Ï£ºÏÑù ÎòêÎäî Î°úÍ∑∏ Ï∂îÍ∞Ä
      /* ignore product spec error */
    }
  })

  const finalNormal = {}
  Object.keys(tempNormal).forEach(k => { finalNormal[k] = Array.from(tempNormal[k]).sort() })
  const finalOther = {}
  Object.keys(tempOther).forEach(k => { finalOther[k] = Array.from(tempOther[k]) })
  const finalWidthRaw = Array.from(tempWidthRaw).sort()
  const finalWidthRanges = {}
  Object.keys(tempWidthRanges).sort((a,b) => parseInt(a) - parseInt(b)).forEach(k => { finalWidthRanges[k] = Array.from(tempWidthRanges[k]) })
  return { normalSpecs: finalNormal, otherSpecs: finalOther, widthRaw: finalWidthRaw, widthRanges: finalWidthRanges }
})

const normalSpecs = computed(() => specs.value.normalSpecs)
const otherSpecs = computed(() => specs.value.otherSpecs)
const widthRaw = computed(() => specs.value.widthRaw)
const widthRanges = computed(() => specs.value.widthRanges)
const hasSpecs = computed(() => widthRaw.value.length > 0 || Object.keys(normalSpecs.value).length > 0 || Object.keys(otherSpecs.value).length > 0)

const updateFilter = (key, value, checked) => {
  if (!selectedSpecs.value[key]) selectedSpecs.value[key] = new Set()
  if (checked) selectedSpecs.value[key].add(value)
  else {
    selectedSpecs.value[key].delete(value)
    if (selectedSpecs.value[key].size === 0) delete selectedSpecs.value[key]
  }
  emitChange()
}

const updateMultiValueFilter = (key, values, checked) => {
  if (!selectedSpecs.value[key]) selectedSpecs.value[key] = new Set()
  if (checked) values.forEach(v => selectedSpecs.value[key].add(v))
  else {
    values.forEach(v => selectedSpecs.value[key].delete(v))
    if (selectedSpecs.value[key].size === 0) delete selectedSpecs.value[key]
  }
  emitChange()
}

const isRangeSelected = (key, values) => {
  if (!selectedSpecs.value[key]) return false
  return values.every(v => selectedSpecs.value[key].has(v))
}

const emitChange = () => {
  const emitData = {}
  Object.keys(selectedSpecs.value).forEach(k => { emitData[k] = Array.from(selectedSpecs.value[k]) })
  emit('filter-change', emitData)
}
</script>

<style scoped>
.spec-filter {
  margin-bottom: 15px;
  padding: 10px;
  background: #fdfdfd;
  border: 1px solid #eee;
  border-radius: 4px;
}
.filter-group {
  margin-bottom: 8px;
  padding-bottom: 6px;
  border-bottom: 1px solid #f5f5f5;
}
.filter-group:last-child { border-bottom: none; }
.filter-title {
  font-weight: bold;
  font-size: 12px;
  margin-bottom: 5px;
  color: #555;
  display: flex;
  align-items: center;
  cursor: pointer;
}
.title-text { flex: 1; }
.toggle-arrow { margin-right: 5px; font-size: 9px; color: #aaa; width: 10px; }
.precision-toggle {
  font-weight: normal;
  font-size: 10px;
  cursor: pointer;
  color: #42b983;
  background: #f0f9f5;
  padding: 1px 6px;
  border-radius: 4px;
  display: flex;
  align-items: center;
  gap: 3px;
}
.filter-options {
  display: flex;
  flex-wrap: wrap;
  gap: 6px;
  padding-left: 15px;
}
.filter-label {
  font-size: 11px;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 3px;
  color: #777;
}
.filter-label input { width: 12px; height: 12px; margin: 0; }
.filter-label:hover { color: #42b983; }
</style>